.Sidebar {
    height: 100vh;
    width: 13%;
    background-color: #fafbfd;
}

.title { 
    display: flex;
    justify-content: center;
    margin-top: 36px;
    font-size: 14px;
    font-family: 'Poppins', sans-serif;
}

.dev { color: #f7542e; }

.challenges { color: #090f31; }

.main-nav {
    display: flex;
    justify-content: center;
    margin-top: 120px;
}

.main-nav ul li {
    list-style-type: none;
    color: #9e9e9e;
    margin-top: 24px;
}

.main-nav ul li:nth-of-type(4) { 
    color: #090f31;
    font-weight: 700;
}